import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common/index';
import { Card, Row, Col, message } from 'antd';
import { getQueryParams } from 'utils/urlUtil';
import Btn from 'components/common/button/Btn';
import FormSearchRankConfig from './FormSearchRankConfig';
import { getRankConfigs, deleteRankConfig, getUser, downloadFile } from '../../services/rankConfig';

class ListRankConfigPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
    getUser()
      .then(res => {
        this.setState({
          token: res.data.accessToken,
        });
      })
      .catch(error => {
        console.log(error);
      });
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  urlFormNew = '#/rank-config/new';
  urlFormEdit = '#/rank-config/edit';

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleDelete = async id => {
    const { token } = this.state;
    await deleteRankConfig(id, token);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  exportRankConfig = () => {
    const { paramSearch, token } = this.state;
    downloadFile(paramSearch, token);
  };

  render() {
    const columns = [
      {
        title: 'Mã ngạch',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Chức danh',
        dataIndex: 'label',
        key: 'label',
      },

      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`${this.urlFormEdit}/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="">
        <CartHeadAction>
          <BtnAddNew href={this.urlFormNew} />
        </CartHeadAction>
        <FormSearchRankConfig />
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={getRankConfigs}
              columns={columns}
              rowKey="id"
            />
          </Col>
          <Col style={{ textAlign: 'center' }} span={24}>
            <Btn onClick={this.exportRankConfig} icon="download">
              Download
            </Btn>
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <ListRankConfigPage {...props} />
  </Page>
));



